.login {
  width: 100%;
  height: 100%;
  background: #020024;
  background: -webkit-gradient(linear, left top, right top, from(#020024), color-stop(0%, #097938), color-stop(100%, #56f0aa), to(#00d4ff));
  background: linear-gradient(90deg, #020024 0%, #097938 0%, #56f0aa 100%, #00d4ff 100%);
}
.login .login-header {
  display: flex;
  align-items: center;
  height: 80px;
  background-color: rgba(21, 20, 13, 0.5);
}
.login .login-header img {
  width: 60px;
  height: 60px;
  margin: 0 15px;
}
.login .login-header h1 {
  font-size: 30px;
  color: #fff;
}
.login .login-content {
  width: 400px;
  height: 300px;
  background-color: #fff;
  margin: 50px auto;
  padding: 20px 40px;
}
.login .login-content h2 {
  text-align: center;
  font-size: 30px;
  font-weight: bold;
}
.login .login-content .login-form .login-form-button {
  width: 100%;
}
